.firstBlock {
  background-color: whitesmoke;
  border: 2px solid rgba(210, 1, 72, 0.78);
  padding: 5px 25px;
  box-sizing: border-box;
  position: absolute;
  top: 40px;
  right: 20px;
  border-radius: 10px;
  overflow: hidden;
}
.firstBlock .secondBlock {
  background-color: #EF5151;
  position: absolute;
  top: 0;
  height: 100%;
  right: -80px;
  border-radius: 5px;
  width: 100%;
  transition: 0.8s linear;
  z-index: 10;
}
.firstBlock .btnArrowLeft {
  position: relative;
  width: 25px;
  height: 20px;
  color: #5959bd;
  filter: drop-shadow(0px 1px 15px #9e9ff3);
  z-index: 11;
}
.firstBlock:hover {
  cursor: pointer;
}
.firstBlock:hover .secondBlock {
  right: 0;
}

